import { high } from 'nc-lightapp-front';

const { Refer } = high;

export default function (props = {}) {
    var conf = {
        multiLang: {
            domainName: 'hpf',
            currentLocale: 'zh-CN',
            moduleId: 'M0Z10205',
        },
        isCacheable: false,
        refType: 'grid',
        refName: '来源支付机构类别',
        placeholder: '来源支付机构类别',
        queryGridUrl: '/nccloud/hpf/payorg/PayorgGridRefQuery.do',
        columnConfig: [{ name: ["支付机构编码","支付机构名称","来源系统编码","来源系统名称"], code: ["code", "name","syscode","sysname"]}],/* 国际化处理： 编码,名称*/
        isMultiSelectedEnabled: false,
        isHasDisabledData: false
    };

    return <Refer {...conf} {...props} />
}